
.active{
  background-color: #4ab6b5 !important;
  color: #fff !important;
}
.fitstDiv{
  background-color: #fff;
  height: 5.5rem;
  width: 100%;
  margin-top: 0.5rem;
}
.examstatus{
  width: 92%;
  margin: auto;

  margin-bottom: 0.75rem;
}
.examTit{
  padding-top: 0.8rem;
  color: #000;
  font-size: 0.65rem;
  padding-bottom: 0.35rem;
}
.examBtn{
  height: 2rem;
  line-height: 2rem;
  margin-top: 0.4rem;
}
.examBtn .statusBtn{
  display: inline-block;
  width: 4.2rem;
  height: 1.75rem;
  text-align: center;
  line-height: 1.75rem;
  border-radius: 5px;
  background-color:#ededed ;
  color: #bcbcbc;
  font-size: 0.7rem;
  margin-right: 1rem;
}
.secDiv{
  margin-top: 0.5rem;
  height: 11.5rem;
  background-color: #fff;
}
.uploadVideo{
  width: 92%;
  margin: auto;
}
.up{
  width: 100%;
  height: 9.4rem;
  text-align: center;
  position: relative;
}
.upmv{
  position: absolute;
  top: 0px;
  left: 0px;
  right: 0px;
  bottom: 0px;
  z-index: 2;
}
.jia{
  width: 3.1rem;
  height: 3.1rem;
  padding-bottom: 0.6rem;
  padding-top: 2rem;
}
.upmsg{
  font-size: 0.6rem;
  color: #c9c9c9;
  font-weight: lighter;
}
.upmsg2{
  font-size: 0.8rem;
  color: #c9c9c9;
}
.up .vidCon{
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0px;
  left: 0px;
  bottom: 0px;
  right: 0px;
  z-index: 100;
}
.up .close{
  position: absolute;
  right: 0rem;
  top: -1.5rem;
  display: inline-block;
  width: 1.5rem;
  height: 1.5rem;
  z-index: 1000;
}
.up .close img{
  width: 100%;
  height: 100%;
}
.up .vidCon video{
  width: 100%;
  height: 100%;
  object-fit:fill;
}
.thirdDiv{
  margin-top: 0.5rem;
  background-color: #fff;
  padding-bottom: 2rem;
}
.takemsg{
  width: 92%;
  margin: auto;
}
.setMsg{
  width: 95%;
  height: 5.5rem;
  background-color: #ededed;
  padding:0.2rem 0.5rem;
}
.setMsg textarea{
  background-color: #ededed;
  width: 100%;
  height: 100%;
  color: #c9c9c9;
  border: none;
  outline: none;
  font-size: 0.7rem;
  resize:none;
}
::-webkit-input-placeholder { /* WebKit browsers */
color: #c9c9c9;
}
.sure{
  /*position: fixed;
  bottom: 0px;
  left: 0px;
  right: 0px;*/
  width: 92%;
  margin: auto;
  height: 2.5rem;
  z-index: 10;
  background-color: #4ab6b5;
  color: #fff;
  text-align: center;
  line-height: 2.5rem;
  font-size: 0.7rem;
  border-bottom: 1rem solid #fff;
}
.sureCon{
  width: 100%;
  background-color: #fff;
}



